  position: absolute;
  background-color: #e1d4c9;
  width: 10vw;
  right: 0;
  transition: all 2s ease;
}

.burger__list {
  padding: 0;
  font-size: 16;
  font-weight: 600;
  line-height: 24px;
  font-style: normal;
  font-family: "Inter", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.burger__item {
  padding-bottom: 60px;
  list-style-type: none;
}

@media (max-width: 768px) {
  .burger__btn {
    display: flex;
  }
}
.--active-burger {
  opacity: 1;
  right: 40px;
  width: 90vw;
}

.--active-btn-burger {
  transition: all 1s ease;
}

.burger__btn.--active-btn-burger .burger__line:nth-child(1) {
  transition: all 1s ease;
  transform: translateY(4px) rotate(-45deg);
}
.burger__btn.--active-btn-burger .burger__line:nth-child(2) {
  transition: all 1s ease;
  transform: translateY(-3px) rotate(45deg);
}

.menu__reboot {
  display: none;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 60px;
  height: 60px;
  border: 1px solid #665f55;
  border-radius: 100px;
  margin-bottom: 100px;
}

@media (max-width: 768px) {
  .menu__reboot {
    display: flex;
  }
}
.reboot--active {
  animation-name: make-circle;
  animation-duration: 3s;
}

.reboot--hide {
  opacity: 0;
}

.coffee__slider {
  width: 100%;
  display: flex;
  transition: all 1s ease;
}

.slider__item {
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}

@media (hover: hover) {
  .menu__drinks-item:hover .menu__drinks-img {
    transform: scale(1.1);
  }
  .left-container__img-item:hover {
    background-color: #e1d4c9;
  }
  .left-container__img-item:hover .left-container__img-svg {
    stroke: #403f3d;
  }
  .icon__item-tea:hover {
    background-color: #665f55;
  }
  .icon__item-tea:hover .container_for__icon-tea {
    background-color: #e1d4c9;
  }
  .icon__item-desert:hover {
    background-color: #665f55;
  }
  .icon__item-desert:hover .container_for__icon-dessert {
    background-color: #e1d4c9;
  }
  .arrow-left:hover {
    background: #665f55;
  }
  .arrow-left:hover .arrow-left__svg {
    stroke: #e1d4c9;
  }
  .arrow-right:hover {
    background: #665f55;
  }
  .arrow-right:hover .arrow-right__svg {
    stroke: #e1d4c9;
  }
  .enjoy__btn_meny:hover .enjoy__btn_text {
    right: 98px;
  }
  .enjoy__btn_meny:hover::after {
    transition: all 0.7s ease;
    opacity: 1;
  }
  .img-container__item:hover {
    transform: scale(1.1);
  }
  .nav__item-link:hover::before,
  .nav__item-link:hover::after,
  .footer__link:hover::after,
  .footer__link:hover::before {
    width: 50%;
  }
}
.modal__wrapper {
  position: fixed;
  display: flex;
  margin: 0 auto;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  background: rgba(64, 63, 61, 0.8);
  z-index: -1;
  bottom: 0;
  transition: all 1s ease;
  justify-content: center;
  align-items: center;
}

.modal__wrapper--active {
  z-index: 5;
  opacity: 1;
  transition: all 1s ease;
}

.modal__container {
  position: fixed;
  display: flex;
  margin: auto;
  left: -9999px;
  max-width: 800px;
  border-radius: 40px;
  background-color: #e1d4c9;
  z-index: 10;
  color: #403f3d;
  transition: all 1s ease;
}

.modal--active {
  left: 0;
  right: 0;
  transition: all 1s ease;
}

.modal__img_container {
  width: 75%;
}

.modal__img_wrapper {
  overflow: hidden;
  height: 310px;
  width: 310px;
  margin: 16px;
  border-radius: 40px;
}

.modal__img {
  height: 100%;
  width: 100%;
  transform: scale(1.1);
  -o-object-fit: contain;
     object-fit: contain;
  transition: all 0.5s ease;
}

.modal__img:hover {
  transform: scale(1);
}

.modal__content {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-left: 5px;
  padding-right: 16px;
}

.modal__sizes {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding-bottom: 21px;
  gap: 8px 0;
}

.modal__additives_title,
.modal__sizes_title {
  margin-bottom: 6px;
}

.size__container {
  display: flex;
  align-items: center;
  border-radius: 100px;
  height: 45px;
  margin-right: 7px;
  border: 1px solid #c1b6ad;
  transition: all 0.5s ease;
}

.size__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 100px;
  background: #c1b6ad;
  margin: 0 8px 0px 7px;
  transition: all 0.5s ease;
}

.size--active {
  background: #665f55;
}
.size--active .size__icon {
  background: #e1d4c9;
}
.size--active .icon__text {
  color: #e1d4c9;
}

.modal__additives {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 24px;
  gap: 8px 0;
}

.additives__container {
  display: flex;
  align-items: center;
  border-radius: 100px;
  height: 45px;
  margin-right: 7px;
  border: 1px solid #c1b6ad;
  transition: all 0.5s ease;
}

.additives__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 100px;
  background: #c1b6ad;
  margin: 0 8px 0px 7px;
  transition: all 0.5s ease;
}

.additives__active {
  background: #665f55;
}
.additives__active .additives__icon {
  background: #e1d4c9;
}
.additives__active .icon__text {
  color: #e1d4c9;
}

.modal__total_container {
  display: flex;
  justify-content: space-between;
  padding-bottom: 21px;
}

.modal__warning_container {
  display: flex;
  border-top: 1px solid #c1b6ad;
  padding-top: 12px;
}

.warning__icon {
  margin-right: 8px;
}

.modal__btn_close {
  display: flex;
  justify-content: center;
  margin: 32px 0 14px;
  padding: 9px 0;
  border: 1px solid;
  border-radius: 40px;
}

@media (max-width: 768px) {
  .modal__container {
    max-width: 688px;
  }
}
@media (max-width: 700px) {
  .modal__wrapper {
    height: 100vh;
  }
  .modal__container {
    max-width: 342px;
  }
  .modal__img_container {
    display: none;
  }
  .modal__content {
    padding-left: 17px;
  }
}
.enjoy__video {
  position: absolute;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 40px;
}

html {
  scroll-behavior: smooth;
}

.stop--scroll {
  overflow: hidden;
